


























































import {request, getQueryString, showToast} from 'common/js/common'
import {getAdvList, getCalllMsg, bindPhone, sendMoveCarCode} from 'common/js/requestUrl'

export default {
	data() {
		return {
			latitude: '',
			longitude: '',
			mapContext: {},
			called: false,//是否已经打过电话了
			callout: {
				bgColor: 'red'
			},
			covers: [],
			list: [],
			mainItem: null,
			id: '',
			type: ''				
		};
	},
	onShow(){
	},
	onLoad(options){
		uni.getLocation({
		  type: 'gcj02',
		  success: res => {
			this.latitude = res.latitude
			this.longitude = res.longitude
			this.covers.push({
				id: 0,
				latitude: this.latitude,
				longitude: this.longitude,
				iconPath: '/static/img/icon/Location.png',
				width: '60rpx',
				height: '60rpx',
				callout:{
					content: '我的位置',
					display: 'ALWAYS',
					padding: 10,
					x: -40,
					y: -40,
					bgColor: '#fff',
					borderRadius: 10
				},
			})
		  }
		})
		this.mapContext = uni.createMapContext('map',this)
		this.getAdvList()
		// 其他
		let link = decodeURIComponent(options.q)
		this.changeData(link)
	},
	methods: {
		cancleCall(){
			if(this.called){
				this.mainItem = null
			}
		},
		changeData(link){
			let paramArr = link.split('=');
			if (paramArr.length == 2){
				var params = paramArr[1].split('_');
				this.id = params[0];
				this.type =params[1];
			}
			
			if(this.id && this.type){
				this.getCalllMsg()
			}
			
		},
		changePosition(){
			this.mapContext.moveToLocation()
		},
		openScan(){
			uni.scanCode({
				onlyFromCamera: true,
				success: (res) => {
					this.changeData(res.result)
				}
			});
		},
		navicateToPath(url){
			uni.navigateTo({
				url: '/'+ url
			});
		},
		chooseLocation(latitude, longitude){
			uni.openLocation({
				latitude: parseFloat(latitude),
				longitude: parseFloat(longitude),
				success: function () {
					console.log('success');
				}
			});
		},
		getAdvList(){
			request(getAdvList, {},(res)=> {
				if(res.code == 200){
					this.list = res.result.data
					res.result.data.forEach((v) => {
						this.covers.push({
							id: v.id,
							latitude: v.latitude,
							longitude: v.longitude,
							// iconPath: '/static/img/icon/Location.png',
							iconPath: v.img,
							width: '60rpx',
							height: '60rpx',
							callout:{
								content: v.title,
								display: 'ALWAYS',
								padding: 10,
								x: -40,
								y: -40,
								bgColor: '#fff',
								borderRadius: 10
							},
						})
					})
				}
			})
		},
		getCalllMsg(){
			request(getCalllMsg,{
				id : this.id,
				type: this.type
			},(res) => {
				// 450表示未绑定
				if(res.code == 450){
					uni.navigateTo({
						url: '../callPhone/callPhone?id='+this.id+'&type='+this.type
					});
				}else if(res.code == 204){
					uni.switchTab({
						url: '../tabbar-5/tabbar-5'
					});	
				}else if(res.code == 200){
					this.mainItem = res.result
				}else{
					showToast(res.msg)
				}
			})
		},
		bindPhone(){
			request(bindPhone,{
				id : this.id,
				type: this.type
			},(res) => {
				if(res.code == 200){
					uni.makePhoneCall({
						phoneNumber: res.result //仅为示例
					});
					this.called = true
				}
			})
		},
		sendMoveCarMsg(){
			request(sendMoveCarCode,{
				id : this.id,
				type: this.type,
				msg: '挡住了道路，请挪下车'
			},(res) => {
				if(res.code == 200){
					showToast('短信发送成功')
				}else{
					showToast(res.msg)
				}
			})
		},
		callAdvPhone(phone){
			uni.makePhoneCall({
				phoneNumber: phone //仅为示例
			});
		},
		navToWebView(id, title){
			uni.navigateTo({
				url: '../web-view/web-view?id='+ id +'&title=' + title
			});
		}
	}
};
